/*********** ここまでベースCSS・リセットCSS ***********/
.carousel {
  width: 600px;
  height: calc(600px * 0.5625);
  position: relative;
  margin: 0 auto; }

.carousel-area {
  /* リスト数×リスト幅を計算してwidth指定してもいいが、汎用性を考慮してjQueryで計算代入する */
  height: 100%;
  position: absolute;
  display: flex; }

/* object-fitを使用せずに画像トリミングさせるためにbackgroundで指定 */
.carousel-list {
  width: 600px;
  height: 100%;
  margin-right: 30px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat; }
  .carousel-list img {
    width: 100%;
    height: 100%;
    object-fit: cover; }

@media screen and (max-width: 600px) {
  .carousel {
    width: 300px;
    height: calc(300px * 0.5625); }
  .carousel-list {
    width: 300px;
    height: calc(300px * 0.5625);
    margin-right: 0; } }

/*********** スライド送りボタン ***********/
/* 共有パーツ */
.arow-wrap {
  width: 90%;
  height: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.arrow-btn {
  width: 48px;
  height: 48px;
  background-color: rgba(113, 135, 245, 0.8);
  border-radius: 50%;
  transition: .2s; }

.arrow-btn:focus {
  box-shadow: 0px 1px 10px -2px rgba(0, 0, 0, 0.8); }

.arrow-btn:hover {
  background-color: #334fd8;
  box-shadow: 0px 1px 10px -2px rgba(0, 0, 0, 0.8); }

/* 左 */
.arrow-left {
  position: relative; }

.arrow-left:before {
  content: "";
  width: 10px;
  height: 10px;
  border-top: 2px solid #fefefe;
  border-left: 2px solid #fefefe;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-30%, -50%) rotate(-45deg); }

/* 右 */
.arrow-right {
  position: relative; }

.arrow-right:before {
  content: "";
  width: 10px;
  height: 10px;
  border-top: 2px solid #fefefe;
  border-left: 2px solid #fefefe;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-70%, -50%) rotate(135deg); }

@media screen and (max-width: 600px) {
  .arrow-btn {
    width: 30px;
    height: 30px; } }

/*********** ページネーション ***********/
.pagination {
  width: 150px;
  margin: 5% auto 0;
  display: flex;
  justify-content: space-around; }

.pagination-circle {
  width: 5px;
  height: 5px;
  border: 1px solid #333;
  border-radius: 50%;
  background-color: rgba(83, 97, 223, 0.3); }

/* jsでtargetクラスがついたら背景色を変える */
.pagination-circle.target {
  background-color: rgba(83, 97, 223, 0.8); }
